import React,{useEffect,useState} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';

const Blog = () => {
    const [blogs,setBlogs] = useState([])
    const [featureBlog,setFeatureBlog] = useState([])

    useEffect(() => {
        const fetchData = async()=>{
            try{
                const res = await axios.get(`${process.env.React_APP_API_URL}/api/blog/featured/1`)
                setFeatureBlog(res.data[0]);
            }
            catch(err) {
                console.log(err);
            }
        }
        fetchData();
    }, [])
    
    useEffect(() => {
        const fetchBlog = async () => {
            try {
                const res = await axios.get(`${process.env.React_APP_API_URL}/api/blog/`)
                setBlogs(res.data);
                console.log(res.data);
            }
            catch (err) {
                console.log(err);
            }
        }
        fetchBlog();
    }, [])

    const upeerCaseLatter = (word) =>{
        if(word){
            return word.charAt(0).toUpperCase() +word.slice(1);
        }
        return ''
    }
    const getBlog = ()=>{
        let list = [];
        let resualt = [];
        blogs.map(blogpost => {
            return list.push(
                <div className="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
                    <div className="col p-4 d-flex flex-column position-static">
                        <strong className="d-inline-block mb-2 text-primary">{upeerCaseLatter(blogpost.category)}</strong>
                        <h3 className="mb-0">{blogpost.title}</h3>
                        <div className="mb-1 text-muted">{blogpost.month} {blogpost.day}</div>
                        <p className="card-text mb-auto"> {blogpost.exerpt} </p>
                        <Link to={`/blog/${blogpost.slug}/`} className="stretched-link">Continue reading</Link>
                    </div>
                    <div className="col-auto d-none d-lg-block">
                        <img width="200" height="250" src={blogpost.thumbnail} alt="thumnail blog post"  />
                    </div>
                </div>
            )
        })
        for (let i=0 ;i <list.length; i+=2){
            resualt.push(
                <div key={i} className="row mb-2">
                    <div className="col-md-6">
                        {list[i]}
                    </div>
                    <div className="col-md-6">
                        {list[i + 1] ? list[i+1] : null }
                    </div>
                </div>
            )
        }
        return resualt;
    }
    return(
        <div className="container m-3">
            <div className="nav-scroller py-1 mb-2">
                <nav className="nav d-flex justify-content-between">
                    <Link className="p-2 text-muted" to='/category/world/'>World</Link>
                    <Link className="p-2 text-muted" to='/category/environment/'>Environment</Link>
                    <Link className="p-2 text-muted" to='/category/busnise/'>Busnise</Link>
                    <Link className="p-2 text-muted" to='/category/technology/'>Technology</Link>
                    <Link className="p-2 text-muted" to='/category/games/'>Games</Link>
                    <Link className="p-2 text-muted" to='/category/embroidery/'>Embroidery</Link>
                    <Link className="p-2 text-muted" to='/category/healthy/'>Healthy</Link>
                </nav>
            </div>
            <div className="jumbotron p-4 p-md-5 text-white rounded bg-dark">
                <div className="col-md-6 px-0">
                    <h1 className="display-4 font-italic">{featureBlog.title}</h1>
                    <p className="lead my-3"> {featureBlog.exerpt}</p>
                    <p className="lead mb-0">
                        <Link to={`/blog/${featureBlog.slug}`} className="text-white font-weight-bold">Continue reading...
                        </Link>
                    </p>
                </div>
            </div>
            {getBlog()}
        </div>
    )

};

export default Blog;